import React from "react";
import TalkToUsNew from "./talk-to-us-new";

const CtaButton = (props) => {
  const { db, dynamicHeaderText } = props;
  const defaultHeaderText =
    "Take Your Business to the <br/> Next Level With Us!";
  return (
    <div className="cta_btns_bg">
      <div className="cta_wrapper"> 
        <div className="cta_content">
          <h3
            className="cta_content_title"
            dangerouslySetInnerHTML={{
              __html: dynamicHeaderText || defaultHeaderText,
            }}
          ></h3>
          <button type="submit" className="cta_btn_div">
            <TalkToUsNew label="Talk to us" isFor="banner" db={db} />
          </button>
        </div>
        <div className="cta_eclipse"></div>
      </div>
    </div>
  );
};

export default CtaButton;
